import React, { Component } from 'react';
import './contactline.css';

class ContactLine extends Component {
    render() {
        switch(this.props.contactLineData.class) {
            case "phone":
                return <div class='phoneline'><i className={"ms-Icon ms-Icon--" + this.props.contactLineData.icon} aria-hidden='true'></i>{this.props.contactLineData.number}</div>
            case "linkedin":
                return <div class='linkedinline'><a href={this.props.contactLineData.url} className="linkedinlink"><img className="contacticon linkedinicon" src={require('../photos/linkedin-icon.png')}/> {this.props.contactLineData.url}</a></div>
            case "email":
                return <div class='emailline'><a href={"mailto:"+this.props.contactLineData.address} className="emaillink"><i className={"ms-Icon ms-Icon--" + this.props.contactLineData.icon} aria-hidden='true'></i>{this.props.contactLineData.address}</a></div>
            case "skype":
                return <div class='skypeline'><a href={"skype:"+this.props.contactLineData.profilename+"?chat"}><i className={"ms-Icon ms-Icon--" + this.props.contactLineData.icon} aria-hidden='true'></i>{this.props.contactLineData.profilename}</a></div>
            default:
                return null
       }
    }
}

export default ContactLine;



