import React, { Component } from 'react';
import './contentlist.css';
var Carousel = require('react-responsive-carousel').Carousel;
import 'react-responsive-carousel/lib/styles/carousel.css';
import MediaQuery from 'react-responsive';

class ContentList extends Component {
    imageLayout(imgUrl, imgAlt)
    {
        if (imgUrl!=null)
        {
            var imgScr=require('../photos/'+imgUrl)
            return (
                <img className="contentitem-image" src={imgScr} alt={imgAlt}/>
            );
        } else return null;
    }
    plainTextLayout(txt, className)
    {
        if (txt!=null)
        {
            return (
                <div className={className} dangerouslySetInnerHTML={{__html: txt}} />
            );
        } else return null;
    }
    exampleLayout(txt, className)
    {
        if (txt!=null)
        {
            return (
                <div className={className}>
                    <h5>Example:</h5>
                    <div dangerouslySetInnerHTML={{__html: txt}} />
                </div>
            );
        } else return null;
    }
    render() {
        var contentSlideItems=this.props.contentListData.contentItems.map((contentItemData, index)=>
            <div className="text-slide" key={index}>
                <h4 className="slide-header ms-font-m-plus ms-fontWeight-semibold">{contentItemData.title}</h4>
                {this.imageLayout(contentItemData.image,contentItemData.title)}
                {this.plainTextLayout(contentItemData.text,"slide-plaintext")}
                {this.exampleLayout(contentItemData.example,"slide-exampletext")}
            </div>
        );
        var contentGridItems=this.props.contentListData.contentItems.map((contentItemData, index)=>
            <div className="contentitem-col ms-Grid-col ms-u-sm6 ms-u-md6 ms-u-lg6 ms-u-xl4 ms-u-xxl3" key={index}>
                <h4 className="slide-header ms-font-m-plus ms-fontWeight-semibold">{contentItemData.title}</h4>
                {this.imageLayout(contentItemData.image,contentItemData.title)}
                {this.plainTextLayout(contentItemData.text,"slide-plaintext")}
                {this.exampleLayout(contentItemData.example,"slide-exampletext")}
            </div>
        );
        return (
            <div className="contentlist">
                <h3 className="contentlist-header ms-font-l">{this.props.contentListData.label}</h3>
                <MediaQuery query='(max-width: 650px)'>
                    <Carousel axis="horizontal" showThumbs={false} showStatus={false} infiniteLoop={true}>
                        {contentSlideItems}
                    </Carousel>
                </MediaQuery>
                <MediaQuery query='(min-width: 651px)'>
                    <div className="contentlist-grid ms-Grid-row">
                        {contentGridItems}
                    </div>
                </MediaQuery>
            </div>
        );
    }
}

export default ContentList;



