import React, { Component } from 'react';
import './header.css';
import personaImageSmall from '../photos/persona-small.jpg';
import personaImageBig from '../photos/persona-big.jpg';
import MediaQuery from 'react-responsive';
import PageNav from './pagenav';
import { IconButton, IButtonProps } from 'office-ui-fabric-react/lib/Button';

class PageHeader extends Component {
    constructor(props) {
        super(props);
        this.state = {
            stickedHeader: false,
            scrollTop: 0,
            showHeader: false,
        };
        this.toggleNavClick = this.toggleNavClick.bind(this)
    };
    tick() {
        var minScrollForSticking = 930;
        var stickedHeader=document.body.scrollTop>=minScrollForSticking;
        //var showHeader=document.body.scrollTop<this.state.scrollTop||(this.state.showHeader&&document.body.scrollTop===this.state.scrollTop);
        var showHeader=this.state.showHeader&&document.body.scrollTop===this.state.scrollTop;
        this.setState({
            stickedHeader: stickedHeader,
            showHeader: showHeader,
            scrollTop: document.body.scrollTop
        });
    };
    componentDidMount() {
        this.timerID = setInterval(
            () => this.tick(),
            300
        );
    };
    toggleNavClick() {
        this.setState({
            showHeader: !this.state.showHeader
        })
    };
    render() {
        var showHeaderClassName=this.state.showHeader?" force":"";
        if (!this.state.stickedHeader)
            return (
                <header className="header">
                    <div className="header-grid ms-Grid-row">
                        <div className="photo-col ms-Grid-col ms-u-sm12 ms-u-md12 ms-u-lg12 ms-u-xl4">
                            <figure className="photo-border">
                                <MediaQuery query='(max-width: 640px)'>
                                    <img src={personaImageSmall} className="photo" alt={this.props.pageData.personName}/>
                                </MediaQuery>
                                <MediaQuery query='(min-width: 641px)'>
                                    <img src={personaImageBig} className="photo" alt={this.props.pageData.personName}/>
                                </MediaQuery>
                            </figure>
                        </div>
                        <div className="header-anouncearea ms-Grid-col ms-u-sm12 ms-u-md12 ms-u-lg12 ms-u-xl8">
                            <h1 className="header-title ms-font-xxl">{this.props.pageData.personName}</h1>
                            <div className="header-person-anouncement ms-font-xl">{this.props.pageData.personAnouncement}</div>
                            <div className="header-person-smalltext ms-font-xl">{this.props.pageData.smallText}</div>
                        </div>
                        <div className="nav-col ms-Grid-col ms-u-sm12">
                            <PageNav pageData={this.props.pageData} sticked={this.state.stickedHeader} className="pagenav" />
                        </div>
                    </div>
                </header>
            ); else {
                var toggleNavIcon=this.state.showHeader?"Cancel":"Waffle"
                return (
                    <header className={`header sticked${showHeaderClassName}`}>
                        <IconButton
                            className="togglenav"
                            data-automation-id="togglenav"
                            iconProps={ { iconName: toggleNavIcon } }
                            onClick={this.toggleNavClick}
                        />
                        <div className="topPanel">
                            <figure className="photo-border">
                                <img src={personaImageSmall} className="photo" alt={this.props.pageData.personName}/>
                            </figure>
                            <div className="middleheaderarea">
                                <h1 className="header-title ms-font-xxl">{this.props.pageData.personName}</h1>
                            </div>
                        </div>
                        <PageNav pageData={this.props.pageData} sticked={this.state.stickedHeader} className="pagenav" />
                    </header>
                );
            }
    }
}

export default PageHeader;



